import React from 'react';
import TextField from 'material-ui/TextField';

const Search = ({inputCity}) => {
    return (
        <div>
            <TextField
                floatingLabelText="输入城市"
                onKeyPress={e => e.key === 'Enter' && e.target.value ? inputCity(e.target.value) : 'shanghai'}
            />
        </div>
    );
};

export default Search;


