import React, { Component } from 'react';
import $ from 'jquery';
import injectTapEventPlugin from 'react-tap-event-plugin';
injectTapEventPlugin();
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import Paper from 'material-ui/Paper';

import Search from './components/Search';
import Result from './components/Result';

const paperStyle = {
  minHeight: 600,
  width: 360,
  margin: '20px auto',
  textAlign: 'center'
};

class App extends Component {
  constructor() {
    super()
    this.state = {
      weatherInfo: []
    }
    this.handleInput = this.handleInput.bind(this)
  }

  // https://api.map.baidu.com/telematics/v3/weather?location=shanghai&output=json&ak=1jtCldI4kOO8oGjHItv3F7UVbVjYS7NC
  // fetch api  
  handleInput(city) {
    $.ajax({
      url: `https://api.map.baidu.com/telematics/v3/weather?location=${city}&output=json&ak=1jtCldI4kOO8oGjHItv3F7UVbVjYS7NC`,
      dataType: 'jsonp'
    })
    .done(data => this.setState({
      weatherInfo: data.results
    }))
    .always( data => {
        console.log(data);
        console.log(data.results);
    });
  }

  render() {
    return (
      <MuiThemeProvider>
        <div>
          <AppBar
            title="React Weather App"
            showMenuIconButton={false}
          />
          <Paper style={paperStyle} zDepth={5}>
            <Search inputCity={this.handleInput} />
            <Result weatherInfo={this.state.weatherInfo} />
          </Paper>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default App;



