import React from 'react';
import {List, ListItem} from 'material-ui/List';


/* <!-- weatherInfo: data.results  --> */

const Result = ({weatherInfo}) => (
    weatherInfo.length 
    ? 
    <List>
        <ListItem>
            当前城市: 
            {weatherInfo[0].currentCity}
        </ListItem>
        <hr/>
        <ListItem>
            空气质量：
            PM2.5值 = 
            {weatherInfo[0].pm25}
        </ListItem>
        <hr/>
        <ListItem>
            <h3>今天的天气情况：</h3>
            日期: 
            {weatherInfo[0].weather_data[0].date}<br/>
            白天: 
            <img src={weatherInfo[0].weather_data[0].dayPictureUrl} alt="dayPictureUrl" /><br/>
            夜间: 
            <img src={weatherInfo[0].weather_data[0].nightPictureUrl} alt="nightPictureUrl" /><br/>
            天气: 
            {weatherInfo[0].weather_data[0].weather}<br/>
            风向风力: 
            {weatherInfo[0].weather_data[0].wind}<br/>
            温度: 
            {weatherInfo[0].weather_data[0].temperature}<br/>
        </ListItem>
        <hr/>
        <ListItem>
            {weatherInfo[0].index[0].title}
            :
            {weatherInfo[0].index[0].zs}<br/>
            {weatherInfo[0].index[0].tipt}
            :
            {weatherInfo[0].index[0].des}<br/>
        </ListItem>
        <hr/>
        <ListItem>
            {weatherInfo[0].index[1].title}
            : 
            {weatherInfo[0].index[1].zs}<br/>
            {weatherInfo[0].index[1].tipt}
            : 
            {weatherInfo[0].index[1].des}<br/>
        </ListItem>
        <hr/>
        <ListItem>
            {weatherInfo[0].index[2].title}
            : 
            {weatherInfo[0].index[2].zs}<br/>
            {weatherInfo[0].index[2].tipt}
            : 
            {weatherInfo[0].index[2].des}<br/>
        </ListItem>
        <hr/>
        <ListItem>
            {weatherInfo[0].index[3].title}
            :
            {weatherInfo[0].index[3].zs}<br/>
            {weatherInfo[0].index[3].tipt}
            :
            {weatherInfo[0].index[3].des}<br/>
        </ListItem>
        <hr/>
        <ListItem>
            {weatherInfo[0].index[4].title}
            :
            {weatherInfo[0].index[4].zs}<br />
            {weatherInfo[0].index[4].tipt}
            :
            {weatherInfo[0].index[4].des}<br />
        </ListItem>
        <hr/>
        <hr/>
        <ListItem>
            <h3>明天的天气情况：</h3>
            日期: 
            {weatherInfo[0].weather_data[1].date}<br/>
            白天: 
            <img src={weatherInfo[0].weather_data[1].dayPictureUrl} alt="dayPictureUrl" /><br/>
            夜间: 
            <img src={weatherInfo[0].weather_data[1].nightPictureUrl} alt="nightPictureUrl" /><br/>
            天气: 
            {weatherInfo[0].weather_data[1].weather}<br/>
            风向风力: 
            {weatherInfo[0].weather_data[1].wind}<br/>
            温度: 
            {weatherInfo[0].weather_data[1].temperature}<br/>
        </ListItem>
        <hr/>
        <ListItem>
            <h3>后天的天气情况：</h3>
            日期: 
            {weatherInfo[0].weather_data[2].date}<br />
            白天: 
            <img src={weatherInfo[0].weather_data[2].dayPictureUrl} alt="dayPictureUrl" /><br />
            夜间: 
            <img src={weatherInfo[0].weather_data[2].nightPictureUrl} alt="nightPictureUrl" /><br />
            天气: 
            {weatherInfo[0].weather_data[2].weather}<br />
            风向风力: 
            {weatherInfo[0].weather_data[2].wind}<br />
            温度: 
            {weatherInfo[0].weather_data[2].temperature}<br />
        </ListItem>
        <hr/>
    </List>
    :
    <div></div>
)

export default Result;


